	module.exports = {"data":{"post":{"html":"<p>We're still learning how to use the new camera (Nikon Coolpix 800, by the way)... good thing that we still have a couple of weeks to try things out. Here's a view of the future baby bed (should last for the first 6 months or so).</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/1999/12/dotty.jpg\" height=\"300\" width=\"400\" border=\"0\" alt=\"dotty.jpg: Dotty the spotted cow\" /></p>\n<p>Our cradle is on loan from Roland and Kirsten (stuffed animals not included). The baby blanket with the cats is my original baby blanket, sent in by my folks in their Christmas package.</p>\n<p>The blue cow was my present to Mama the last time she was in the hospital... 1986 in Minneapolis. It was an ad promotion by the Polka Dot Dairy, and is likely the only one in Germany.</p>","fields":{"title":"Ready and Waiting...","date":"07 Dec 1999","url":"https://papascott.de/1999-12-07-ready-and-waiting/"}}},"pathContext":{"slug":"/1999-12-07-ready-and-waiting/","prev":{"fields":{"title":"Week 37","date":"06 Dec 1999","slug":"/1999-12-06-week-37/"}},"next":{"fields":{"title":"Linux fever...","date":"09 Dec 1999","slug":"/1999-12-09-linux-fever/"}}}}

